/* Collapsible */

.Collapsible {
  width: 90%;
  margin: 25px auto;
  border-bottom: 1px solid lightGrey;
  padding: 0px;
  cursor: pointer;
}

.Collapsible__contentInner {
  padding: 10px;
  border: 1px solid lightGrey;
  border-top: 0;
}
.Collapsible__contentInner p {
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 20px;
}

.Collapsible__trigger {
  display: block;
  font-weight: 300;
  letter-spacing: 1px;
  position: relative;
  padding: 10px;
  text-align: left;
  font-size: 24px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #f7631b;
}

.CustomTriggerCSS {
  background-color: lightcoral;
  -webkit-transition: background-color 200ms ease;
  -o-transition: background-color 200ms ease;
  transition: background-color 200ms ease;
}

.CustomTriggerCSS--open {
  background-color: darkslateblue;
}

.Collapsible__custom-sibling {
  padding: 5px;
  font-size: 12px;
  background-color: #cbb700;
  color: black;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.8);
}
.popup_inner {
  position: absolute;
  left: 5%;
  right: 5%;
  top: 15%;
  bottom: 5%;
  margin: auto;
  padding-bottom: 3%;
  background: black;
}

.topicInfo {
  text-align: left;
  padding: 5px;
  font-size: 18px;
  cursor: pointer;
}

.topicInfo a {
  color: black;
}



